(function() {
  'use strict';

  angular
    .module('funnel')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
        // .state('home', {
        //     url: '/',
        //     templateUrl: 'app/main/main.html',
        //     controller: 'MainController',
        //     controllerAs: 'main'
        // })
        .state('test', {
              url: '/',
              templateUrl: 'app/test/test.html',
              controller: 'TestController',
              controllerAs: 'test'
          });

    $urlRouterProvider.otherwise('/');
  }

})();

